/*=============== GOOGLE FONTS ===============*/
@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&display=swap");
/*=============== VARIABLES CSS ===============*/
:root {
  /*========== Colors ==========*/
  --hue: 240;
  --first-color: hsl(var(--hue), 16%, 18%);
  --first-color-alt: hsl(var(--hue), 16%, 12%);
  --title-color: hsl(var(--hue), 8%, 15%);
  --text-color: hsl(var(--hue), 8%, 35%);
  --body-color: hsl(var(--hue), 100%, 99%);
  --container-color: #fff;
  /*========== Font and typography ==========*/
  --body-font: "Poppins", sans-serif;
  --big-font-size: 1.5rem;
  --normal-font-size: 0.938rem;
  /*========== z index ==========*/
  --z-modal: 1001;
}
@media screen and (min-width: 968px) {
  :root {
    --big-font-size: 1.75rem;
    --normal-font-size: 1rem;
  }
}

.c-cookies-config-modal *, .c-cookies-config-banner * {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.c-cookies-config-modal button, .c-cookies-config-banner button {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  cursor: pointer;
  border: none;
  outline: none;
}
.c-cookies-config-modal img, .c-cookies-config-banner img {
  max-width: 100%;
  height: auto;
}

/*=============== MODAL ===============*/
.c-cookies-config-modal {
  display: none;
  /* Show modal */
  /* ===== Scrollbar CSS ===== */
  /* Firefox */
  /* Chrome, Edge, and Safari */
}
.c-cookies-config-modal * {
  font-family: var(--body-font);
}
.c-cookies-config-modal.show-modal {
  display: block;
}
.c-cookies-config-modal .modal__button {
  display: inline-block;
  background-color: var(--first-color);
  color: #fff;
  padding: 1rem 1.25rem;
  border-radius: 0.5rem;
  transition: 0.3s;
}
.c-cookies-config-modal .modal__button:hover {
  background-color: var(--first-color-alt);
}
.c-cookies-config-modal .modal__container {
  position: fixed;
  top: 0;
  left: 0;
  background-color: hsla(var(--hue), 18%, 75%, 0.8);
  width: 100%;
  height: 100%;
  display: grid;
  align-items: flex-end;
  overflow: hidden;
  transition: all 0.3s;
  z-index: var(--z-modal);
  visibility: hidden;
  opacity: 0;
  /* === Effect 3 === */
  /* perspective: 1000px; */
}
.c-cookies-config-modal .modal__container.show-modal {
  visibility: visible;
  opacity: 1;
}
.c-cookies-config-modal .modal__container.show-modal .modal__content {
  /* === Effect 1 === */
  transform: translateY(0);
  /* === Effect 2 === */
  /* transform: scale(1) translateY(0); */
  /* === Effect 3 === */
  /* transform: rotateX(0) scale(1) translateY(0); */
}
.c-cookies-config-modal .modal__cookie-categories {
  display: flex;
  flex-direction: column;
  max-height: 40vh;
  padding-right: 20px;
  overflow: auto;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category:not(:first-child) {
  margin-top: 20px;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header {
  margin-bottom: 5px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__right,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__right,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__right {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__right.no-chevron,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__right.no-chevron,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__right.no-chevron {
  margin-left: 24px;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__left,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__left,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__left {
  margin-right: 16px;
  content: "";
  width: 18px;
  height: 18px;
  background-image: url('data:image/svg+xml,<?xml version="1.0"?><svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-chevron-right" viewBox="0 0 16 16"><path fill-rule="evenodd" d="M4.646 1.646a.5.5 0 0 1 .708 0l6 6a.5.5 0 0 1 0 .708l-6 6a.5.5 0 0 1-.708-.708L10.293 8 4.646 2.354a.5.5 0 0 1 0-.708z"/></svg>');
  background-repeat: no-repeat;
  background-size: cover;
  transition-timing-function: ease-out;
  transition: all 0.2s;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__title,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__title,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__title {
  font-size: 20px;
  font-weight: 500;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch {
  transform: scale(0.7);
  /* The switch - the box around the slider */
  /* Hide default HTML checkbox */
  /* The slider */
  /* Rounded sliders */
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch .switch,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch .switch,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch .switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch .switch.disabled,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch .switch.disabled,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch .switch.disabled {
  opacity: 0.6;
  cursor: not-allowed;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch .switch input,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch .switch input,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch .switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch .slider,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch .slider,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch .slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch .slider.disabled,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch .slider.disabled,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch .slider.disabled {
  cursor: not-allowed;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch .slider:before,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch .slider:before,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch .slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch input:checked + .slider,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch input:checked + .slider,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch input:checked + .slider {
  background-color: var(--first-color);
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch input:focus + .slider,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch input:focus + .slider,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch input:focus + .slider {
  box-shadow: 0 0 1px var(--first-color);
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch input:checked + .slider:before,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch input:checked + .slider:before,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch .slider.round,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch .slider.round,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch .slider.round {
  border-radius: 34px;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__header__switch .slider.round:before,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .header__switch .slider.round:before,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header__switch .slider.round:before {
  border-radius: 50%;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__body,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .body {
  font-size: 14px;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category__body--boxed,
.c-cookies-config-modal .modal__cookie-categories .cookie-category .body--boxed {
  padding: 0px 10px;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cm-accordion {
  cursor: pointer;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header.cm-boxed {
  padding: 10px;
  background-color: rgba(39, 39, 53, 0.118);
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header.cm-boxed:hover {
  background-color: rgba(39, 39, 53, 0.2);
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header.cm-boxed.cm-active {
  background-color: rgba(39, 39, 53, 0.2);
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cc-header.cm-active .cc-header__left {
  transform: rotate(90deg);
}
.c-cookies-config-modal .modal__cookie-categories .cookie-category .cm-panel {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.2s ease-out;
}
.c-cookies-config-modal .modal__content {
  position: relative;
  background-color: var(--container-color);
  padding: 3rem 2rem 2rem;
  border-radius: 1rem 1rem 1rem 1rem;
  transition: all 0.3s;
  /* === Effect 1 === */
  transform: translateY(10%);
  /* === Effect 2 === */
  /* transform: scale(.5) translateY(10%); */
  /* === Effect 3 === */
  /* transform-origin: 50% 100%; */
  /* transform: rotateX(65deg) scale(.75) translateY(10%); */
}
.c-cookies-config-modal .modal__footer {
  margin-top: 50px;
  display: flex;
  gap: 12px;
}
@media screen and (max-width: 767px) {
  .c-cookies-config-modal .modal__footer {
    flex-direction: column;
  }
  .c-cookies-config-modal .modal__footer button {
    width: 100%;
  }
  .c-cookies-config-modal .modal__footer .footer__button-accept-all {
    order: 1;
  }
  .c-cookies-config-modal .modal__footer .footer__button-reject-all {
    order: 2;
  }
  .c-cookies-config-modal .modal__footer .footer__button-save-btn {
    order: 3;
  }
}
.c-cookies-config-modal .modal__img {
  width: 150px;
  margin-bottom: 0.75rem;
}
.c-cookies-config-modal .modal__close {
  display: inline-flex;
  background-color: white;
  border-radius: 0.25rem;
  color: #fff;
  font-size: 1.5rem;
  position: absolute;
  top: 2rem;
  right: 2rem;
  cursor: pointer;
}
.c-cookies-config-modal .modal__close img,
.c-cookies-config-modal .modal__close svg {
  background-color: white;
}
.c-cookies-config-modal .modal__close .close-modal-img {
  width: 20px;
  height: 20px;
  color: white;
  background-image: url("data:image/svg+xml;base64,");
}
.c-cookies-config-modal .modal__title {
  font-size: var(--big-font-size);
  color: var(--title-color);
  font-weight: 500;
}
.c-cookies-config-modal .modal__description {
  margin-bottom: 1.5rem;
}
.c-cookies-config-modal .modal__button-width {
  width: 60%;
}
.c-cookies-config-modal .modal__button-link {
  display: block;
  background-color: transparent;
  color: var(--first-color);
  font-weight: 500;
  width: 40%;
  height: 55px;
}
.c-cookies-config-modal * {
  scrollbar-width: auto;
  scrollbar-color: #272735 #ffffff;
}
.c-cookies-config-modal *::-webkit-scrollbar {
  width: 16px;
}
.c-cookies-config-modal *::-webkit-scrollbar-track {
  background: #ffffff;
}
.c-cookies-config-modal *::-webkit-scrollbar-thumb {
  background-color: #272735;
  border-radius: 10px;
  border: 3px solid #ffffff;
}

.c-cookies-config-banner * {
  font-family: var(--body-font);
}
.c-cookies-config-banner .banner-container {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: var(--container-color);
  width: 95%;
  display: none;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  transition: all 0.3s;
  z-index: 1000;
  flex-direction: column;
  left: 50%;
  transform: translateX(-50%);
  bottom: 25px;
  border-radius: 1em;
  padding: 30px;
}
.c-cookies-config-banner .banner-container p {
  align-self: flex-start;
}
.c-cookies-config-banner .banner-container__button {
  display: inline-block;
  background-color: var(--first-color);
  color: #fff;
  padding: 1rem 1.25rem;
  border-radius: 0.5rem;
  transition: 0.3s;
}
.c-cookies-config-banner .banner-container__button:hover {
  background-color: var(--first-color-alt);
}
.c-cookies-config-banner .banner-container__button-link {
  display: block;
  background-color: transparent;
  color: var(--first-color);
  font-weight: 500;
  padding: 1rem 1.25rem;
}
.c-cookies-config-banner .banner-container.show-banner {
  display: flex;
}
.c-cookies-config-banner .banner-container p {
  text-align: left;
}
.c-cookies-config-banner .banner-container__buttons {
  margin-top: 25px;
  display: flex;
  width: 100%;
  gap: 12px;
}
@media screen and (max-width: 767px) {
  .c-cookies-config-banner .banner-container__buttons {
    flex-direction: column;
  }
}
@media screen and (max-width: 767px) {
  .c-cookies-config-banner .banner-container__accept-all-btn {
    order: 1;
  }
  .c-cookies-config-banner .banner-container__reject-all-btn {
    order: 2;
  }
  .c-cookies-config-banner .banner-container__config-btn {
    order: 3;
  }
}

.c-cookies-config-wall {
  display: none;
  background-color: hsla(var(--hue), 18%, 75%, 0.8);
  height: 100%;
  position: fixed;
  width: 100%;
  z-index: 10;
  top: 0;
}
.c-cookies-config-wall--blurred {
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}
.c-cookies-config-wall.wall-show {
  display: block;
}

/*=============== BREAKPOINTS ===============*/
/* For small devices */
@media screen and (min-width: 767px) {
  .modal__content {
    margin: auto;
    width: 500px;
    border-radius: 1.25rem;
  }
  .modal__img {
    width: 170px;
  }
}